.footer {
  background: #000 url("footer-bg.4c4defb0.jpg") center / cover no-repeat;
  padding-top: 68px;
}

.footer__inner {
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}

.footer__content {
  flex-direction: column;
  display: flex;
}

.footer__title {
  font-family: var(--font-family-third), Arial, sans-serif;
  color: #0000;
  -webkit-text-fill-color: transparent;
  letter-spacing: .02em;
  background: linear-gradient(61deg, #fff 0%, #ff5f20 100%) text;
  width: 100%;
  max-width: 572px;
  font-size: 40px;
  font-weight: 500;
  line-height: 1.2;
}

.footer__description {
  color: #fffc;
  width: 100%;
  max-width: 335px;
  margin-top: 30px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.4;
}

.footer__tg {
  background-color: var(--orange-100, #ff5f20);
  width: max-content;
  font-family: var(--font-family-fifth), Arial, sans-serif;
  letter-spacing: -.04em;
  text-transform: uppercase;
  color: var(--base-content-100, #fff);
  border-radius: 20px;
  align-items: center;
  gap: 20px;
  height: 60px;
  margin-top: 80px;
  padding: 10px 10px 10px 24px;
  font-size: 13px;
  font-weight: 600;
  line-height: 1.4;
  display: flex;
}

.footer__big-text {
  font-family: var(--font-family-fourth), Arial, sans-serif;
  text-transform: uppercase;
  text-align: center;
  color: var(--base-content-100, #fff);
  word-break: break-word;
  text-wrap: nowrap;
  width: 100%;
  margin-top: 108px;
  font-size: 18vw;
  font-weight: 800;
  line-height: 1.2;
  position: relative;
}

.footer__big-text sup {
  font-size: 4vw;
  line-height: 1;
  position: absolute;
  top: 17%;
  right: 0;
}

.footer__big-text--small {
  color: var(--base-content-100, #fff);
  width: 100%;
  max-width: 296px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
}

@media (width <= 768px) {
  .footer {
    padding-top: 42px;
    padding-bottom: 10px;
  }

  .footer__inner {
    flex-direction: column;
    justify-content: flex-start;
  }

  .footer__big-text {
    margin-top: 140px;
    font-size: 18.5vw;
  }

  .footer__big-text sup {
    margin: 0;
    font-size: 5vw;
  }

  .footer__tg, .footer__description {
    margin-top: 40px;
  }

  .footer__title {
    letter-spacing: .02em;
    font-size: 24px;
    font-weight: 500;
    line-height: 1.2;
  }

  .footer__big-text--small {
    display: none;
  }
}

@media (width >= 1460px) {
  .footer__big-text {
    font-size: 265px;
  }

  .footer__big-text sup {
    font-size: 60px;
  }
}
